import { Component } from 'react'
import { connect } from 'react-redux'

class _MovesList extends Component {
  state = {
    moves: null,
    user: null
  }
  componentDidMount() {
    this.loadMoves()
  }
  componentDidUpdate(prevProps, prevState) {
    if(this.props.user.coins !== prevProps.user.coins || this.props.user.coins !== prevState.user?.coins) {
      this.loadMoves()
    }
  }
  loadMoves() {
    console.log('loadsing MOVES');
    const {user, contact} = this.props
    var moves = contact ? user.moves.filter(move => move.toId === contact._id) : user.moves
    moves = moves.map(m => {
      var d = new Date(m.at)
      m.date = `${(d.getDate())}-${(d.getMonth() + 1)}-${(d.getFullYear())}, ${(d.getHours() + '').padStart(2, 0)}:${(d.getMinutes() + '').padStart(2, 0)}:${(d.getSeconds() + '').padStart(2, 0)}`
      return m
    })
    this.setState({moves, user: {...user}})
  }
  render() {
    const {moves} = this.state
    const {contact, user} = this.props
    if(!moves || moves.length === 0) return <div>No moves to show</div>
    return (
      <section className="moves-list">
        <h4>Your moves:</h4>
        {moves.map(m => {
          return (
            <div className="user-move" key={m.at}>
              {!contact && <div><span>To: </span>{m.to}</div>}
              <div><span>At: </span>{m.date}</div>
              <div className="amount"><span>Amount: </span>{m.amount}$</div>
            </div>
          )
        })}
      </section>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    user: state.userModule.loggedInUser,
  }
}

export const MovesList = connect(mapStateToProps)(_MovesList)
