import { Component } from 'react'
import { connect } from 'react-redux'
import { signup } from '../store/actions/userActions'

class _SignupPage extends Component {
  state = {
    name: '',
  }
  inputRef = (elInput) => {
    if (elInput) elInput.focus()
  }
  signUp = (ev) => {
    ev.preventDefault()
    this.props.signup(this.state.name)
    this.props.history.push('/')
  }
  handleChange = async ({ target }) => {
    const name = target.value
    this.setState({name})
  }
  render() {
    const { name } = this.state
    return (
      <section className="signup-page main-content">
        <form onSubmit={this.signUp}>
          <div className="large-avatar"></div>
          <label htmlFor="name">Please enter your name</label>
          <input ref={this.inputRef} onChange={this.handleChange} value={name} type="text" name="name" id="name" required />
          <button>Sign up</button>
        </form>
      </section>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    user: state.userModule.loggedInUser,
  }
}

const mapDispatchToProps = {
  signup
}

export const SignupPage = connect(mapStateToProps, mapDispatchToProps)(_SignupPage)