import { Component } from 'react'
import contactService from '../../services/contactService'
import { Link } from 'react-router-dom'
import { TransferFund } from '../../components/Moves/TransferFund'
import { MovesList } from '../../components/Moves/MovesList'

export class ContactDetails extends Component {
  state = {
    contact: null,
  }
  componentDidMount() {
    this.loadContact()
  }
  componentDidUpdate(prevProps, prevState) {
    if (prevProps.match.params.id !== this.props.match.params.id) {
      this.loadContact()
    }
  }
  async loadContact() {
    const contact = await contactService.getContactById(this.props.match.params.id)
    this.setState({ contact })
  }
  onBack = () => {
    this.props.history.push('/contact')
    // this.props.history.goBack()
  }
  render() {
    const { contact } = this.state
    if (!contact) return <div>Loading..</div>
    return (
      <section className="contact-details main-layout">
        <div className="large-avatar"></div>
        <div>
          <span>Name:</span> {contact.name}
        </div>
        <div>
          <span>Phone:</span> {contact.phone}
        </div>
        <div>
          <span>Email:</span> {contact.email}
        </div>
        <TransferFund contact={contact}/>
        <nav>
          <button onClick={this.onBack}>Go back</button>
          <Link to={`/contact/edit/${contact._id}`}><button>Edit</button></Link>
        </nav>
        <Link to="/contact/edit" className="add-btn" title="Add contact"/>
        <MovesList contact={contact}/>
      </section>
    )
  }
}


