import { Component } from 'react'
import { connect } from 'react-redux'
import { Link } from 'react-router-dom'
import { ContactList } from '../../components/Contact/ContactList'
import { ContactFilter } from '../../components/Contact/ContactFilter'
import { loadContacts, removeContact, setFilterBy } from '../../store/actions/contactActions'

class _ContactPage extends Component {

  async componentDidMount() {
    this.props.loadContacts()
  }

  onFilter = async (filterBy) => {
    await this.props.setFilterBy(filterBy)
    this.props.loadContacts()
  }
  render() {
    const { contacts } = this.props
    if (!contacts) return <div>Loading..</div>
    return (
      <section className="contact-page main-layout main-content">
        <ContactFilter onFilter={this.onFilter} />
        <ContactList contacts={contacts} />
        <Link to="/contact/edit" className="add-btn" title="Add contact"/>
      </section>
    )
  }
}


const mapStateToProps = state => {
  return {
      contacts: state.contactModule.contacts
  }
}

const mapDispatchToProps = {
  loadContacts,
  removeContact,
  setFilterBy,
}

export const ContactPage = connect(mapStateToProps, mapDispatchToProps)(_ContactPage)