import { Component } from 'react'
import { ConfirmedTransactions } from '../components/Chart/ConfirmedTransactions'
import { MarketPrice } from '../components/Chart/MarketPrice'
import { bitcoinService } from '../services/bitcoinService'

export class StatisticPage extends Component {
  state = {
    marketPrice: null,
  }
  componentDidMount() {
    this.createData()
  }
  initialData = [
    { time: '2018-12-22', value: 32.51 },
    { time: '2018-12-23', value: 31.11 },
    { time: '2018-12-24', value: 27.02 },
    { time: '2018-12-25', value: 27.32 },
    { time: '2018-12-26', value: 25.17 },
    { time: '2018-12-27', value: 28.89 },
    { time: '2018-12-28', value: 25.46 },
    { time: '2018-12-29', value: 23.92 },
    { time: '2018-12-30', value: 22.68 },
    { time: '2018-12-31', value: 22.67 },
  ]
  async createData() {
    const data = await bitcoinService.getMarketPrice()
    const marketPrice = data.values.map((date) => {
      return {
        time: date.x,
        value: date.y,
      }
    })
    const data2 = await bitcoinService.getConfirmedTransactions()
    const confirmedTransactions = data2.values.map((date) => {
      return {
        time: date.x,
        value: date.y,
      }
    })
    this.setState({ marketPrice, confirmedTransactions })
  }
  render() {
    const { confirmedTransactions, marketPrice } = this.state
    return (
      <section className="statistic-page main-layout main-content">
        <div>
          <h2>Confirmed Transactions</h2>
          {confirmedTransactions && <ConfirmedTransactions data={confirmedTransactions} />}
        </div>
        <div>
          <h2>Market Price</h2>
          {marketPrice && <MarketPrice data={marketPrice} />}
        </div>
      </section>
    )
  }
}
