import { storageService } from './storageService'

const key = 'user_db'

var gUser = storageService.load(key) || {}

function getUser() {
  return gUser
}

function signup(name) {
  if (!name) return
  const user = {
    name,
    coins: 100,
    moves: [],
  }
  storageService.store(key, user)
  gUser = user
  return gUser
}

function addMove(contact, amount) {
  if (gUser.coins < amount) return
  const move = {
    toId: contact._id,
    to: contact.name,
    at: Date.now(),
    amount,
  }
  gUser.coins -= amount
  gUser.moves.unshift(move)
  storageService.store(key, gUser)
  return gUser
}

export const userService = {
  getUser,
  addMove,
  signup
}
