import { Component } from 'react'
import { connect } from 'react-redux'

import { addMove } from '../../store/actions/userActions'

class _TransferFund extends Component {

  state = {
    amount: 0
  }

  handleChange = ({ target }) =>{
    console.log('value', target.value);
    const amount = +target.value
    this.setState({amount})
  }
  
  transfer = (ev) =>{
    ev.preventDefault()
    console.log('at TRANSFER');
    this.props.addMove(this.props.contact, this.state.amount)
  }

  render() {
    const {amount} = this.state
    const {contact} = this.props
    return (
      <section className="transfer-fund">
        <h4>Transfer funds to {contact.name}</h4>
          <form onSubmit={this.transfer}>
            <input onChange={this.handleChange} type="number" placeholder="Amount" />
            <button>Transfer</button>
          </form>
      </section>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    user: state.userModule.loggedInUser,
  }
}

const mapDispatchToProps = {
  addMove,
}

export const TransferFund = connect(mapStateToProps, mapDispatchToProps)(_TransferFund)
