import { Component } from 'react'
import { Link } from 'react-router-dom'
import { connect } from 'react-redux'
import { removeContact, addContact, updateContact } from '../../store/actions/contactActions'
import contactService from '../../services/contactService'

class _ContactEdit extends Component {

  state = {
    contact: null,
  }

  async componentDidMount() {
    const id = this.props.match.params.id
    const contact = id ? await contactService.getContactById(id) : contactService.getEmptyContact()
    this.setState({ contact })
  }

  handleChange = async ({ target }) => {
    const field = target.name
    const value = target.type === 'number' ? +target.value || '' : target.value
    this.setState((prevState) => ({ contact: { ...prevState.contact, [field]: value } }))
  }

  onSaveContact = async (ev) => {
    ev.preventDefault()
    const contact = { ...this.state.contact }
    contact._id ? await this.props.updateContact(contact) : await this.props.addContact(contact)
    this.props.history.push('/contact')
  }

  inputRef = (elInput) => {
    if (elInput) elInput.focus()
  }

  onRemoveContact = async () => {
    await this.props.removeContact(this.state.contact._id)
    this.props.history.push('/contact')
  }

  render() {
    const { contact } = this.state
    if (!contact) return <div>Loading...</div>
    return (
      <section className="contact-edit main-layout main-content">
        <h2>{contact._id ? 'Edit' : 'Add'} Contact</h2>
        <div className="large-avatar"></div>
        <form onSubmit={this.onSaveContact}>
          <label htmlFor="name">Name</label>
          <input ref={this.inputRef} onChange={this.handleChange} value={contact.name} type="text" name="name" id="name" required />

          <label htmlFor="phone">Phone</label>
          <input onChange={this.handleChange} value={contact.phone} type="text" name="phone" id="phone" required />

          <label htmlFor="email">Email</label>
          <input onChange={this.handleChange} value={contact.email} type="email" name="email" id="email" required />

          <button>Save</button>
        </form>
        <div className="btn-container">
          <Link to="/contact" className="goback">
            Go Back
          </Link>
          {contact._id && (
            <button onClick={() => this.onRemoveContact()} className="delete-btn">
              Delete
            </button>
          )}
        </div>
      </section>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    contacts: state.contactModule.contacts,
  }
}

const mapDispatchToProps = {
  removeContact,
  addContact,
  updateContact
}

export const ContactEdit = connect(mapStateToProps, mapDispatchToProps)(_ContactEdit)
