import { Component } from 'react'
import { connect } from 'react-redux'
import { MovesList } from '../components/Moves/MovesList'
import { bitcoinService } from '../services/bitcoinService'

class _HomePage extends Component {
  state = {
    user: null,
    balance: null,
    rate: null,
  }
  async componentDidMount() {
    const user = this.props.user
    const rate = await bitcoinService.getRate('USD')
    this.setState({ user, rate, balance: user.coins * rate })
  }
  render() {
    const { user, balance } = this.state
    if(!user) return
    return (
      <section className="home-page main-layout main-content">
        <h2>Hello, {user.name} !</h2>
        <h3>Coins: {user.coins}</h3>
        <h3><span>BTC</span>: {balance}</h3>
        <MovesList/>
      </section>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    user: state.userModule.loggedInUser,
  }
}
export const HomePage = connect(mapStateToProps)(_HomePage)
