import Axios from 'axios'
import { storageService } from './storageService.js'

var axios = Axios.create({
  withCredentials: false,
})

const KEY = 'coin_rates'
const gRates = storageService.load(KEY) || {}

export const bitcoinService = {
  getRate,
  getMarketPrice,
  getConfirmedTransactions
}

async function getRate(coins) {
  try {
    if (gRates[coins]) return gRates[coins]
    const res = await axios.get(`https://blockchain.info/tobtc?currency=${coins}&value=1`)
    gRates[coins] = res.data
    storageService.store(KEY, gRates)
    return res.data
  } catch (error) {
    console.log(`Cannot get ${coins} rate`, error)
    throw error
  }
}

async function getMarketPrice() {
  try {
    if (gRates.marketPrice) return gRates.marketPrice
    const res = await axios.get(`https://api.blockchain.info/charts/market-price?timespan=1months&format=json&cors=true`)
    gRates.marketPrice = res.data
    storageService.store(KEY, gRates)
    return res.data
  } catch (error) {
    throw error
  }
}
async function getConfirmedTransactions() {
  try {
    if (gRates.getConfirmedTransactions) return gRates.getConfirmedTransactions
    const res = await axios.get('https://api.blockchain.info/charts/transactions-per-second?timespan=1months&rollingAverage=8hours&format=json&cors=true')
    gRates.getConfirmedTransactions = res.data
    storageService.store(KEY, gRates)
    return res.data
  } catch (error) {
    throw error
  }
}
